<template>
  <div>
    <Menu active-name="alabama">
        <Submenu name="stateNetwork">
            <template slot="title">
                <van-icon name="location" />
                Network of States
            </template>
            <Submenu :name="index" v-for="(item, index) in stateName" :key="index">
                <template slot="title">
                  <van-icon name="location-o" v-if="nowState == item" />
                  {{item}}
                </template>
                <MenuItem :name="item+1"><div @click="addNetwork(item)">rail</div></MenuItem>
                <MenuItem :name="item+2">motoway</MenuItem>
            </Submenu>
        </Submenu>
        <Submenu name="gmns">
            <template slot="title">
                <van-icon name="wap-nav" />
                GMNS_Network
            </template>
            <Submenu name="Phoenix">
                <template slot="title">
                  Phoenix
                </template>
                <MenuItem name="agent1">
                  <Checkbox v-model="agent1">Agent1</Checkbox>
                </MenuItem>
                <MenuItem name="agent2">
                  <Checkbox v-model="agent2">Agent2</Checkbox>
                </MenuItem>
                <MenuItem name="macronet">
                  <Checkbox v-model="macronet">macronet</Checkbox>
                </MenuItem>
                <MenuItem name="mesonet">
                  <Checkbox v-model="mesonet">mesonet</Checkbox>
                </MenuItem>
                <MenuItem name="micronet">
                  <Checkbox v-model="micronet">micronet</Checkbox>
                </MenuItem>
            </Submenu>
            <Submenu name="Sioux Falls">
                <template slot="title">
                  Sioux Falls
                </template>
                <MenuItem name="link">
                  <Checkbox v-model="link">Link</Checkbox>
                </MenuItem>
            </Submenu>
        </Submenu>
    </Menu>
  </div>
  
</template>

<script>
import layers from "@/utils/layers.js"
import {Circle as CircleStyle, Fill, Stroke, Style} from 'ol/style';
import {transform} from 'ol/proj';
import GeoJSON from 'ol/format/GeoJSON';
import {Vector as VectorSource} from 'ol/source';
import {Vector as VectorLayer} from 'ol/layer';




export default {
  name: 'cityList',
  components: {
  },
  data() {
    return {
      stateName: ['alabama','alaska','arizona','arkansas','california','colorado','connecticut','delaware','district-of-columbia','florida','georgia','hawaii','idaho','illinois','indiana','iowa','kansas','kentucky','louisiana','maine','maryland','massachusetts','michigan','minnesota','mississippi','missouri','montana','nebraska','nevada','new-hampshire','new-jersey','new-mexico','new-york','north-carolina','north-dakota','ohio','oklahoma','oregon','pennsylvania','puerto-rico','rhode-island','south-carolina','south-dakota','tennessee','texas','utah','vermont','virginia','washington','west-virginia','wisconsin','wyoming'],
      vectorLayer: '',
      agent1Layer: '',
      agent2Layer: '',
      //当前点击的state
      nowState: 'alabama',
      single: false,
      //复选框的状态
      agent1: false,
      agent2: false,
      macronet: false,
      mesonet: false,
      micronet: false,
      link: false,

    }
  },
  mounted() {
    this.addNetwork('alabama');
  },
  //监听复选框的状态
  watch: {
    macronet: function(newData,oldData) {
      if(newData == true) {
        this.addNetwork('net1');
        this.micronet = false;
        this.mesonet = false;
        this.link = false;
      }else {
        this.map.map.removeLayer(this.vectorLayer);
      }
    },
    mesonet: function(newData,oldData) {
      if(newData == true) {
        this.addNetwork('net2');
        this.macronet = false;
        this.micronet = false;
        this.link = false;
      }else {
        this.map.map.removeLayer(this.vectorLayer);
      }
    },
    micronet: function(newData,oldData) {
      if(newData == true) {
        this.addNetwork('net3');
        this.macronet = false;
        this.mesonet = false;
        this.link = false;
      }else {
        this.map.map.removeLayer(this.vectorLayer);
      }
    },
    link: function(newData,oldData) {
      if(newData == true) {
        this.addNetwork('link');
        this.micronet = false;
        this.mesonet = false;
        this.macronet = false;
      }else {
        this.map.map.removeLayer(this.vectorLayer);
      }
    },
    agent1: function(newData,oldData) {
      if(newData == true) {
        this.addNetwork('agent1');
      }else {
        this.map.map.removeLayer(this.agent1Layer);
      }
    },
    agent2: function(newData,oldData) {
      if(newData == true) {
        this.addNetwork('agent2');
      }else {
        this.map.map.removeLayer(this.agent2Layer);
      }
    },
  },
  methods: {
    addNetwork(item) {
      this.nowState = item;
      //解决如果网络多选框没关就点击state的路网导致多选框还存在的问题
      if(item !== "net1" && item !== "net2" && item !== "net3" && item !== "agent1" && item !== "agent2") {
        this.micronet = false;
        this.mesonet = false;
        this.macronet = false;        
      }
      let baseUrl = 'static/link/' + item +  '.json';
      this.$axios({
        method: 'get',
        url: baseUrl,
      }).then( (response) => {
      // console.log(this.map.map.getLayers())
      //先移除上一个图层  
      this.map.map.removeLayer(this.vectorLayer);
      //根据不同的地区设置不同的zoom层级
      let zoomLevel = 8;
      switch(item) {
        case 'net1':
          zoomLevel = 13;
          break;
        case 'net2':
          zoomLevel = 13;
          break;
        case 'net3':
          zoomLevel = 13;
          break;
        case 'link':
          zoomLevel = 13;
          break;
        case 'agent1':
          zoomLevel = 13;
          break;
        case 'agent2':
          zoomLevel = 13;
          break;
        default: 
          zoomLevel = 8;
      };
      this.map.map.getView().animate({
        center: response.data.features[0].geometry.coordinates[0],
        zoom: zoomLevel,
        }
      );
      let linkJSON = (new GeoJSON()).readFeatures(response.data);
      var vectorSource = new VectorSource({
        features: linkJSON
      });
      if(item=="agent1") {
        this.agent1Layer = new VectorLayer({
          source: vectorSource,
          style: new Style({
            stroke: new Stroke({
              color: 'blue',
              width: 3
            }),
            fill: new Fill({
              color: 'rgba(255,255,0)'
            })
          }),
          zIndex: 999
        });
        this.map.map.addLayer(this.agent1Layer);
      }else if(item=="agent2") {
        this.agent2Layer = new VectorLayer({
          source: vectorSource,
          style: new Style({
            stroke: new Stroke({
              color: 'blue',
              width: 3
            }),
            fill: new Fill({
              color: 'rgba(255,255,0)'
            })
          }),
          zIndex: 999
        });  
        this.map.map.addLayer(this.agent2Layer);      
      }else {
        this.vectorLayer = new VectorLayer({
          source: vectorSource,
          style: new Style({
            stroke: new Stroke({
              color: 'rgba(60,139,153)',
              width: 3
            }),
            fill: new Fill({
              color: 'rgba(255,255,0)'
            })
          }),
          zIndex: 999
        });
        this.map.map.addLayer(this.vectorLayer);
      }
      })
    },

}
}
</script>

<style>



</style>



